:root {
  --primaryGreen: hsl(177, 100%, 33%);
  --greenDark: hsl(176, 100%, 10%);
  --greenLight: hsl(176, 50%, 73%);
  --primaryBlue: hsl(196, 100%, 33%);
  --primaryBlueDark: hsl(196, 100%, 10%);
  --menu-shadow: hsla(196, 100%, 10%, 60%);
  --menu-overlay: hsla(196, 100%, 33%, 40%);
  --darkBlue: hsl(196, 100%, 24%);
  --lightBlue: hsl(196, 49%, 73%);
  --primaryOrange: hsl(30, 92%, 54%);
  --darkOrange: hsl(30, 77%, 44%);
  --lightOrange: hsl(31, 91%, 82%);
  --hoverOrange: hsl(30, 77%, 34%);
  --lightGrey: hsl(0, 0%, 92%);
  --medGrey: hsl(0, 0%, 44%);
  --dkGrey: hsl(0, 0%, 25%);
  --black:hsl(0, 0%, 6%);
  --white: hsl(240, 100%, 99%);
  --white60: hsla(240, 100%, 99%, .85);
}

:root {
  --font-heading: "Roboto Slab", Helvetica, Arial, sans-serif; /* heading font */
  --font-text: "Lato", Helvetica, Arial, sans-serif; /* body font */
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-text);
  background-color: var(--white);
  color: var(--black);
}

img {
  display: block;
  width: 100%;
}

figure,
blockquote {
  margin: 0;
}

header ul,
footer ul {
  margin: 0;
  padding: 0;
}
header ul li,
footer ul li {
  list-style-type: none;
}

.emphsis {
  color: var(--primaryBlue);
  font-weight: 700;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}
.visually-hidden:focus {
  left: 0;
  width: auto;
  height: auto;
  background-color: #fff;
  padding: 0.5rem;
  border: 1px solid;
}

.button {
  font-family: var(--font-text);
  font-size: 1.75rem;
  line-height: 1;
  font-weight: 500;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  padding: 1rem 1.5rem;
  display: inline-block;
  text-decoration: none;
  transition-property: background-color, border, color;
  transition-duration: 200ms;
  transition-timing-function: ease-in-out;
}
.button.ghost {
  background-color: var(--white);
  color: var(--darkBlue);
  border: 1px solid var(--darkBlue);
}
.button.ghost:hover {
  background-color: var(--darkBlue);
  color: var(--white);
  border: 1px solid var(--white);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-heading);
  font-weight: 700;
  line-height: 1.1;
  text-wrap: balance;
  margin-top: 0;
  letter-spacing: 0.055px;
}

h1 {
  font-size: 3.75rem;
  font-size: clamp(1.875rem, 1.02rem + 3.632vw, 3.75rem);
  margin-block-end: 1.25rem;
  color: var(--darkBlue);
}

h2,
h3,
h4,
h5,
h6 {
  color: var(--primaryBlue);
}

p {
  font-size: 1rem;
  line-height: 1.5;
  margin-top: 0;
  margin-bottom: 1.25rem;
}

a:visited,
a:active {
  text-decoration: none;
}

a {
  text-decoration: underline;
}

.wrapper {
  width: min(100% - 3rem, 75rem);
  margin-inline: auto;
}
header {
  background-color: var(--white);
  box-shadow: 0 3px 6px var(--dkGrey);
}

.topnav {
  position: sticky;
  top: 0;
}
.topnav, .topnav__menu {
  background-color: var(--white);
  z-index: 10;
}
.topnav__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-block: 0;
}
.topnav__homelink {
  margin: 16px 0 16px;
  cursor: pointer;
}
@media (max-width: 43.74875em) {
  .topnav__homelink {
    z-index: 100;
    width: 65%;
    margin: 0;
  }
}
@media (max-width: 43.74875em) {
  .topnav__overlay {
    position: fixed;
    inset: 0;
    background-color: var(--menu-overlay);
    opacity: 0;
    transition: opacity ease-in-out;
    pointer-events: none;
  }
  .topnav__overlay:has(+ .topnav__open[aria-expanded=true]) {
    opacity: 1;
    pointer-events: all;
  }
}
.topnav__open, .topnav__close {
  background: none;
  padding: 0;
  border: 0;
  cursor: pointer;
}
@media (min-width: 43.75em) {
  .topnav__open {
    display: none;
  }
}
.topnav__open[aria-expanded=true] + .topnav__menu {
  translate: 0 0;
}
@media (prefers-reduced-motion) {
  .topnav__open[aria-expanded=true] + .topnav__menu {
    opacity: 1;
  }
}
@media (max-width: 43.74875em) {
  .topnav__menu {
    position: fixed;
    inset: 0 0 auto 0;
    padding: 50px 24px 60px;
    border-radius: 0 0 20px 20px;
    box-shadow: 0 12px 12px var(--menu-shadow);
    translate: 0 -110%;
    transition: translate ease-in-out;
  }
}
@media (max-width: 43.74875em) and (prefers-reduced-motion) {
  .topnav__menu {
    translate: 0;
    opacity: 0;
    transition-property: opacity;
  }
}
.topnav__close {
  display: none;
}
@media (max-width: 43.74875em) {
  .topnav__close {
    display: block;
    margin-inline-start: auto;
    margin-block-end: 60px;
  }
}
.topnav__links {
  display: flex;
  flex-direction: row;
  gap: 30px;
}
@media (min-width: 43.75em) {
  .topnav__links {
    margin-left: 30px;
  }
}
@media (max-width: 43.74875em) {
  .topnav__links {
    flex-direction: column;
    align-items: center;
  }
}
.topnav__link {
  display: block;
  font-size: 1.125rem;
  line-height: 1;
  font-weight: 400;
  color: var(--primaryBlueDark);
  text-decoration: none;
  cursor: pointer;
}
.topnav__link:hover {
  color: var(--hoverOrange);
  text-decoration: underline;
}

.top__bar {
  background-color: var(--primaryBlue);
  width: 100%;
  height: 20px;
  z-index: 12;
  position: relative;
}

.featureImage {
  background: url(/img/gibsons_hero_1920x750.jpg) no-repeat;
  background-position: center top;
  background-size: cover;
  left: 50%;
  padding: 0;
  position: relative;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  margin-top: 0px;
  margin-bottom: 0px;
  overflow: hidden;
  height: 35.9375rem;
}
@media (max-width: 43.74875em) {
  .featureImage {
    background-position: left top;
    height: 28.125rem;
  }
}

.hero-img {
  width: 100vw;
  height: auto;
  min-height: 18.75rem;
  border-bottom: 5px solid var(--darkBlue);
  overflow: hidden;
  position: relative;
  display: block;
  margin: 0 auto;
  top: 0;
}
@media screen and (max-width: 639px) {
  .hero-img {
    min-height: auto;
  }
}

img.full-width {
  width: 100%;
  height: auto;
  position: relative;
  display: block;
  padding: 0;
  margin: 0;
}

img.full-width {
  width: 100%;
  height: auto;
  position: relative;
  display: block;
  padding: 0;
  margin: 0;
}
@media screen and (max-width: 1023px) {
  img.full-width {
    width: auto;
    height: 45vh;
    left: 50%;
    transform: translate(-50%);
    max-width: none;
    overflow: hidden;
  }
}

.aboutHero {
  margin-bottom: 5rem;
}

h1.intro__heading {
  margin-bottom: 1.5rem;
}

.intro {
  margin-bottom: 5rem;
}
.intro__text {
  display: flex;
  flex-direction: row;
  gap: 16px;
  padding: 0 1.5rem;
  margin-bottom: 3rem;
}
@media (max-width: 43.74875em) {
  .intro__text {
    flex-direction: column;
    padding: 0;
    width: 100%;
  }
}
.intro__img--lg {
  border: 5px solid var(--darkBlue);
  margin-bottom: 3rem;
}
@media (max-width: 43.74875em) {
  .intro__img--lg {
    margin-inline: 0;
    width: 100%;
  }
}
@media (max-width: 43.74875em) {
  .intro__img--lg img {
    object-fit: cover;
    -o-object-fit: cover;
    position: relative;
    transform: translateX(-50%);
    left: 50%;
    overflow: hidden;
  }
}
.intro__featureImgs {
  display: flex;
  flex-direction: row;
  gap: 16px;
}
@media (max-width: 43.74875em) {
  .intro__featureImgs {
    flex-direction: column;
    gap: 2.5rem;
  }
}

.featureImg {
  max-width: 100%;
  height: auto;
  border: 5px solid var(--darkBlue);
  margin-bottom: 16px;
}
.featureImg__link {
  font-family: var(--font-text);
  color: var(--primaryBlue);
  font-weight: 700;
  font-size: 1.125rem;
  text-decoration: none;
  cursor: pointer;
}
.featureImg__link:hover {
  color: var(--darkOrange);
  text-decoration: underline;
}

.galleryPhotos__twoCols {
  display: flex;
  flex-direction: row;
  gap: 16px;
  margin: 0 24px 40px;
}
@media (max-width: 43.74875em) {
  .galleryPhotos__twoCols {
    flex-direction: column;
  }
}
.galleryPhotos__fullWidth {
  margin: 0 24px 40px;
}
@media (max-width: 43.74875em) {
  .galleryPhotos__fullWidth {
    margin: 0 0 40px;
  }
}
@media (max-width: 43.74875em) {
  .galleryPhotos__fullWidth img {
    left: 50%;
    -o-object-fit: cover;
    object-fit: cover;
    overflow: hidden;
    position: relative;
    transform: translateX(-50%);
  }
}

.galleryPhoto__img {
  border: 5px solid var(--darkBlue);
}

.heading__wrapper {
  width: min(100% - 3rem, 50rem);
  margin-inline: auto;
  padding: 2rem 0 1.5rem;
}
@media (max-width: 43.74875em) {
  .heading__wrapper {
    margin-inline: 0;
    width: 100%;
  }
}

.workshop__connect {
  background-color: var(--darkBlue);
  width: 100%;
  padding: 0;
  margin-bottom: 4rem;
}
.workshop__notice {
  display: flex;
  flex-direction: row;
}
@media (max-width: 43.74875em) {
  .workshop__notice {
    flex-direction: column;
  }
}
.workshop__notice img {
  display: inline-block;
  max-width: 100%;
  margin: 40px 0;
}
.workshop__branding {
  margin: 40px 0;
  text-align: center;
  width: 100%;
}
.workshop__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
}
@media (max-width: 43.74875em) {
  .workshop__text {
    align-items: center;
    padding-bottom: 3rem;
  }
}
.workshop__text h3 {
  font-family: var(--font-text);
  font-weight: 500;
  font-size: 1.75rem;
  color: var(--white);
  margin-bottom: 24px;
}

.artists__feature {
  display: flex;
  flex-direction: row;
  gap: 16px;
  margin: 0;
  width: 100%;
}
.artists__feature img {
  border: 5px solid var(--darkBlue);
  height: auto;
  margin-bottom: 16px;
  max-width: 100%;
}
.artists__feature {
  margin-bottom: 4rem;
}
.artists__feature--lg {
  margin: 0 0 3rem 0;
  width: 100%;
}
@media (max-width: 43.74875em) {
  .artists__feature--lg {
    margin-inline: 0;
    width: 100%;
  }
}
@media (max-width: 43.74875em) {
  .artists__feature--lg img {
    object-fit: cover;
    -o-object-fit: cover;
    position: relative;
    transform: translateX(-50%);
    left: 50%;
    overflow: hidden;
  }
}

.visit {
  margin-bottom: 5.5rem;
}
.visit h2 {
  font-family: var(--font-heading);
  font-size: 2.5rem;
  line-height: 1;
  font-weight: 700;
  color: var(--darkBlue);
  text-align: center;
}

.artist__krista {
  border: 5px solid var(--darkBlue);
}

.about__intro {
  display: flex;
  flex-direction: row;
