  padding: 0 1.5rem;
}
.about__col1, .about__col2 {
  width: 100%;
}

h1.artistTitle {
  font-size: 4rem;
  margin-bottom: 1rem;
}

h2.artistName {
  font-size: 2rem;
  margin-bottom: 2.5rem;
}

.footer {
  background-color: var(--lightGrey);
}
.footer__wrapper {
  display: flex;
  flex-direction: row;
}
.footer__content {
  display: flex;
  flex-direction: row;
